@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f1efea;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

.App:focus {
  outline: none;
}

.Intro {
  align-items: center;
  background-color: #282c34;
  border: 5px solid transparent;
  color: #fff;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  min-height: 100vh;
  text-align: center;
}

.Intro a {
  color: #61dafb;
}

.Intro-logo {
  height: 40vmin;
  margin: 30px;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .Intro-logo {
    animation: Intro-logo-spin infinite 20s linear;
  }
}

.is-dropping .Intro {
  background-color: #363c48;
  border-color: #f1cb85;
}

@keyframes Intro-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Profile {
  background: #f1efea;
  border: 5px solid transparent;
  color: #27374c;
  font-family: Lato, 'Helvetica Neue', Helvetica, Arial, 'Lucida Grande', sans-serif;
  font-feature-settings: 'kern', 'liga', 'pnum';
  line-height: 1.5;
  min-height: 100vh;
  padding: 50px;
}

.Profile-header {
  margin: 10px auto;
  max-width: 980px;
}

.Profile a {
  color: #287fb8;
  text-decoration: none;
}

.Profile a:active,
.Profile a:focus,
.Profile a:hover {
  color: #ed6504;
  text-decoration: none;
}

.Profile-tabs {
  margin: 0;
  padding: 0;
  list-style: none;
  padding: 10px 20px;
}

.Profile-tabs li {
  display: inline-block;
  margin: 10px 30px 10px 0;
  vertical-align: middle;
}

.Profile-tabs li button {
  background: none;
  border: 0;
  border-bottom: 2px solid transparent;
  color: #287FB8;
  cursor: pointer;
  font-size: 1em;
  margin: 0;
  outline: none;
  padding: 5px 0;
}

.Profile-tabs li button.is-active,
.Profile-tabs li button:hover,
.Profile-tabs li button:focus {
  color: #ed6504;
}
.Profile-tabs li button.is-active {
  border-color: rgba(0,0,0,0.15);
}

.Profile-tab-count {
  border-radius: 3.75em;
  color: #fff;
  background: #287FB8;
  display: inline-block;
  font-size: 0.8em;
  font-weight: bold;
  line-height: 1;
  margin-left: 10px;
  padding: 4px 7px;
  vertical-align: 1px;
}

.Profile-tabs li button.is-active .Profile-tab-count,
.Profile-tabs li button:hover .Profile-tab-count,
.Profile-tabs li button:focus .Profile-tab-count {
  background: #ed6504;
}

.is-dropping .Profile {
  background-color: #fdf7e7;
  border-color: #ed6504;
}

.is-dropping .Profile * {
  opacity: 0.8;
}

.promo {
  text-align: center;
  font-size: 1.5em;
  font-weight: 300;
  margin-top: 40px;
}

.promo .tr-logo {
  width: 50px;
}

.Section {
  background-color: #fff;
  border-bottom: 2px solid rgba(0,0,0,0.15);
  border-radius: 2px;
  margin: 10px auto;
  max-width: 980px;
}

.Section p {
  margin: 0 0 1em 0;
}

.Section-heading {
  padding: 20px;
  border-bottom: 1px solid #e6e6e6;
  font-size: 1em;
  text-transform: uppercase;
  word-break: break-word;
  font-weight: bold;
}

.Section-content {
  overflow-wrap: break-word;
  padding: 20px;
  word-break: break-word;
}

.Profile-reference {
  padding: 30px 0;
  border-top: 1px solid #e6e6e6;
}

.Profile-reference-meta {
  display: flex;
  justify-content: space-between;
}

.Profile-reference-experience {
  font-weight: bold;
}

.Profile-reference-experience-positive {
  color: #41b866;
}

.Profile-reference-experience-neutral {
  color: #ED6504;
}

.Profile-reference-experience-negative {
  color: #c63d43;
}

.Profile-reference-date {
  color: #929EAE;
  font-size: 0.9em;
}

.AboutMe-interests {
  margin: 10px 0;
  padding: 0;
  list-style: none;
}

.AboutMe-interests li {
  background-color: transparent;
  border-radius: 4px;
  border: 2px solid #bfcad2;
  color: #34495e;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  margin: 4px;
  max-width: none;
  max-width: initial;
  min-width: 0;
  min-width: initial;
  padding: 2px 10px;
  width: auto;
}



